body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-gotham-pro: "Gotham Pro";

  /* font sizes */
  --font-size-2xs: 11px;
  --font-size-xl: 20px;

  /* Colors */
  --color-white: #fff;
  --color-gainsboro: #d9d9d9;
  --color-black: #000;
  --color-gray-100: #292929;
  --color-gray-200: rgba(0, 0, 0, 0.2);
  --color-lightgray: #ccc;

  /* Gaps */
  --gap-4xs: 9px;

  /* Paddings */
  --padding-4xs: 9px;
  --padding-5xs: 8px;
  --padding-smi: 13px;
  --padding-2xs: 11px;
  --padding-xl: 20px;

  /* Border radiuses */
  --br-3xs: 10px;
  --br-5xs: 8px;
  --br-xs-5: 11.5px;
}
